<template>
  <div class="home">
    <div class="border">
      <label>Current account</label>
      <select v-model="currentAccount">
          <option v-for="account in accounts" :value="account">
            {{ account.id }}
          </option>
      </select>
    </div>
    <div class="border" class="transaction-data">
      <h2>Transaction data</h2>
      <div>
        <label for="amount">Amount</label>
        <input type="number" v-model="transaction.amount"
                v-validate="'required'" name="amount"/>
        <span v-show="errors.has('amount')">{{ errors.first('amount') }}</span>
      </div>
      <div>
        <label for="reason">Reason</label>
        <input type="text" v-model="transaction.reason"
                v-validate="'required'" name="reason"/>
        <span v-show="errors.has('reason')">{{ errors.first('reason') }}</span>
      </div>
      <div>
        <button v-on:click="saveTransaction">
              Save transaction
        </button>
      </div>
    </div>
    <button v-on:click="showTransactionList">
        <h2 v-if="transactions.length == 0">Show transaction list</h2>
        <h2 v-else>Hide transaction list</h2>
    </button>
    <div v-if="transactions.length != 0" class="transaction-list" class="border">
      <ul>
        <li v-for="item in transactions" :class="item.type">
            {{item.amount}}
            {{item.reason}}
            {{item.type}}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Home',
  data () {
    return {
      accounts: [],
      transaction: {},
      currentAccount: null,
      transactions: []
    }
  },
  beforeCreate() {
    axios.get('/get-bankAccounts')
    .then((response) => {
        this.accounts = response.data
        this.currentAccount = this.accounts[0]
    })
  },
  methods: {
    saveTransaction: function(event) {
      if (this.errors.length > 0) {
          alert('Please correct the following error(s):')
          return
      }
      this.transaction.bankAccount = this.currentAccount
      axios.post('/save-transaction', this.transaction)
      .then((response) => {
          if (response.data.type == 'REJECTED'){
            alert('Transaction was rejected')
          } else {
            alert('Transaction was accounted')
            this.transaction = {}
          }
      })
    },
    showTransactionList: function(event) {
      if (this.transactions.length > 0){
        this.transactions = []
        return
      }
      axios.get('/get-transactions', {
        params: {
          id: this.currentAccount.id
        }
      })
      .then((response) => {
          this.transactions = response.data
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

  .REJECTED {
      background : red;
  }

  .DEBIT{
      background: green;
  }

  .CREDIT{
        background: yellow;
    }

  .border {
    border: 1px solid green;
  }
</style>



